'use strict';

// MODULES //
var isnan = __var_0__;
var sqrt = __var_1__;
var zeta = __var_2__;
var PI = __var_3__;


// VARIABLES //

var SKEWNESS = 12.0 * sqrt( 6.0 ) * zeta( 3.0 ) / ( PI*PI*PI );


// MAIN //

/**
* Returns the skewness for a Gumbel distribution with location `mu` and scale `beta`.
*
* @param {number} mu - location parameter
* @param {PositiveNumber} beta - scale parameter
* @returns {number} skewness
*
* @example
* var y = skewness( 0.0, 1.0 );
* // returns ~1.14
*
* @example
* var y = skewness( 5.0, 2.0 );
* // returns ~1.14
*
* @example
* var y = skewness( NaN, 1.0 );
* // returns NaN
*
* @example
* var y = skewness( 0.0, NaN );
* // returns NaN
*
* @example
* var y = skewness( 0.0, 0.0 );
* // returns NaN
*/
function skewness( mu, beta ) {
	if (
		isnan( mu ) ||
		isnan( beta ) ||
		beta <= 0.0
	) {
		return NaN;
	}
	return SKEWNESS;
}


// EXPORTS //
export default skewness;
